@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Questrial&display=swap');
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container56 {
    position: relative;
    height: 60vh;
    align-items: center;
}



.words {
    width: 90%;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 60%;
    left: 50%;
    text-align: center;

}

.words span {
    display: inline-block;
    font-family:'Cinzel Decorative', sans-serif;
    font-size: 11vw;
    font-weight: 700;
    color: white;
    letter-spacing: 0.5vw;
    text-shadow: 0 15px 25px rgba(0, 0, 0, 0.2);
    position: relative;
    /* transform: translateY(-100vh); */
    animation: fall 0.8s forwards;
    /* delay iteration-count direction fill-mode; */

}

