.tagline {
    position: relative;
    text-align: center;
    top: 50vh;
    padding-top: 20px;
    /* margin-bottom: 70px; */
    /* left: 35vw; */
}



@media (max-width: 947px) {

    .container56 {
       
        height: 30vh;
      
    }


    .tagline {
        top: 20vh;
        font-size: 10px;
    }
    .words {
       
        top: 60%;
        left: 50%;
        text-align: center;
    
    }
    
}

@keyframes fall {
    100% {
        transform: translateY(0);
    }

}